:root {
  --cubic: linear;
  --shadow: 0px 10px 10px 0px rgba(0, 0, 0, .1);
}

.container.container__hero {
  width: 100%;
  background-color: #ba89a2;
}

.nav-wrapper {
  align-items: center;
  gap: 20px;
  margin-bottom: 40px;
  display: flex;
}

.nav-img {
  width: 80px;
  margin-left: 20px;
  -webkit-transform: translateY(5px);
  transform: translateY(5px);
}

.hero {
  height: 845px;
  color: #f6ef00;
  background-color: #ba89a2;
  background-image: url("hero-bcg-mobile.77baac24.png");
  background-repeat: no-repeat;
  margin-left: -16px;
  margin-right: -16px;
  padding-top: 130px;
  font-family: Avenir Next Cyr, sans-serif;
}

.nav {
  font-family: Avenir Next Cyr, sans-serif;
  font-style: normal;
  font-weight: 600;
  line-height: 96%;
}

.nav-list {
  flex-direction: column;
  gap: 15px;
  display: flex;
}

.burger-menu {
  max-width: 768px;
  width: 100%;
  height: 600px;
  z-index: 100;
  pointer-events: all;
  padding-top: 60px;
  padding-right: 40px;
  position: absolute;
  top: 0;
  right: 0;
}

.burger-menu__wrapper {
  width: 100%;
  height: 100%;
  flex-direction: column;
  align-items: flex-end;
  display: flex;
}

.burger-menu__open-icon, .burger-menu__close-icon {
  fill: #f6ef00;
  position: relative;
}

.burger-menu__open-icon:active, .burger-menu__close-icon:active {
  fill: #f7f6d9;
}

.burger-menu__button {
  width: 40px;
  height: 40px;
  transition: transform .1s var(--cubic);
  background: none;
  border: none;
  margin-bottom: 60px;
  padding: 0;
}

.burger-menu__button:hover {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}

.burger-nav .nav-list {
  width: 180px;
  flex-direction: column;
  gap: 56px;
  display: flex;
}

.nav-link {
  color: #f6ef00;
  transition: all .1s var(--cubic);
  line-height: 96%;
}

.nav-link:hover {
  color: #f7f6d9;
}

.ivent-time {
  text-align: center;
  margin-bottom: 44px;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 135%;
}

.hero-desktop-flower {
  display: none;
}

.hero-logo-icon, .hero-line, .hero-arrow {
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.hero-line, .hero-arrow {
  fill: #ebe757;
}

.hero-logo-icon {
  fill: #f2cc21;
  width: 100%;
  margin-bottom: 176px;
  padding: 0 20px;
}

.hero-text {
  max-width: 270px;
  text-align: center;
  margin: 27px auto;
  font-size: 16px;
  font-weight: 600;
  line-height: 96%;
  display: block;
}

.hero-arrow-link {
  width: 300px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

@media screen and (max-width: 767px) {
  .hero {
    background-position: 100% 100%;
    background-size: 400px;
  }

  .nav {
    font-size: 24px;
  }
}

@media screen and (min-width: 768px) {
  .hero {
    background-position: right -250px;
    background-size: 700px;
    margin-left: -32px;
    margin-right: -32px;
  }

  .nav-link {
    font-size: 24px;
  }

  .ivent-time {
    font-size: 20px;
    font-weight: 600;
  }

  .hero-text {
    font-size: 20px;
  }
}

@media screen and (max-width: 1199px) {
  .nav-wrapper, .hero-arrow-desktop {
    display: none;
  }
}

@media screen and (min-width: 1200px) {
  .nav-wrapper {
    padding-top: 20px;
  }

  .header-nav-list {
    width: 100%;
    flex-direction: row;
    justify-content: flex-start;
    gap: 50px;
    padding: 0;
    display: flex;
  }

  .hero {
    background-image: none;
    margin-left: -48px;
    margin-right: -48px;
    padding-top: 0;
  }

  .burger-menu {
    display: none;
  }

  .hero-logo-icon {
    margin-bottom: 43px;
  }

  .hero-desktop-flower {
    width: 200px;
    margin-left: auto;
    margin-right: auto;
    display: block;
    -webkit-transform: translate(-14px, 20px);
    transform: translate(-14px, 20px);
  }

  .hero-line {
    stroke: #ebe757;
    stroke-width: .5px;
  }

  .hero-arrow {
    transition: all .1s var(--cubic);
    display: none;
  }

  .hero-arrow:hover {
    fill: #f7f6d9;
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
  }

  .hero-arrow.hero-arrow-desktop {
    display: block;
  }
}

