.accordion-item__icon {
  transition: transform .1s var(--cubic);
}

.open {
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
}

.accordion {
  width: 100%;
  margin: 20px auto;
}

.accordion-item {
  background-color: #fff6fb;
  border-bottom: 1px solid;
  border-image: linear-gradient(274deg, rgba(255, 255, 255, 0) 15%, rgba(220, 91, 222, .4) 50%, rgba(255, 255, 255, 0) 85%) 1;
  margin: 10px 0;
}

.accordion-item-header {
  min-height: 50px;
  cursor: pointer;
  justify-content: space-between;
  align-items: center;
  padding: 15px;
  font-family: Alice;
  font-weight: bold;
  line-height: 150%;
  display: flex;
  position: relative;
}

.accordion-item-body {
  max-height: 0;
  transition: min-height .1s ease-out;
  overflow: hidden;
}

.accordion-item-body-content {
  padding: 15px;
  line-height: 150%;
}

@media screen and (min-width: 768px) {
  .person-first__wrapper {
    width: 100%;
    height: auto;
    background-image: none;
  }

  .person-first_info-wrapper {
    width: 100%;
    height: auto;
    flex-direction: row;
    padding: 10px;
  }

  .person-first__photo {
    float: right;
    margin: 10px;
    display: block;
  }

  .person-second__photo {
    display: none;
  }

  .person-second__photo-desktop {
    float: left;
    width: 310px;
    margin: 10px 20px 10px 10px;
    display: block;
  }
}

@media screen and (min-width: 1200px) {
